@import url('https://fonts.googleapis.com/css2?family=Corben:wght@400;700&family=Tajawal:wght@300;400;700;800;900&display=swap');

:root {
    --main-color: #505050;
    --main-color-cont: #ffffff;
    --acc-color-dark: #ff7575;
    --acc-color-dark-rgb: rgb(255, 117, 117);
    --acc-color-shadow: #ff0000;
    --acc-color-gray-dark: #a0a0a0;
    --acc-color-gray-light: #ececec;
    --acc-color-spock: rgb(230, 199, 160);
}

html {
    scroll-behavior: smooth;
}
body {
    margin: 0;
    padding: 0 18rem;
    font-family: 'Tajawal', sans-serif;
    font-size: 20px;
    font-weight: 300;
    background-color: var(--main-color-cont);
    color: var(--main-color);
}
@media (max-width: 420px) {
    body {
        padding: 0 1rem;
    }
}
@media (min-width:421px) and ( max-width:913px) {
    body {
        padding: 0 2rem;
    }
}
a {
    color: var(--acc-color-dark);
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}

#header {
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    flex-wrap: wrap;
}
#header h1 {
    font-family: 'Corben', sans-serif;
    font-size: 1.4rem;
    font-weight: bold;
}
#header h1::after {
    content: '\2665';
    color: var(--acc-color-shadow);
    font-size: 0.8rem;
}
#header span {
    display: block;
    margin-top: -0.8rem;
    margin-bottom: 1.8rem;
}
#header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
}
#header a {
    color: var(--main-color);
    text-transform: uppercase;
}
.current {
    text-decoration: underline var(--acc-color-dark);
}

.date,
.meta {
    font-size: 1.1rem;
}
.content {
    margin-bottom: 3rem;
}
.postcontent ul li,
.pagecontent ul li {
    margin-bottom: 1rem;
}
.postmeta {
    font-size: 1rem;
}
.postlist {
    list-style: none;
}

.pagination {
    margin: 6rem 0 3rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 1 auto;
    flex-wrap: wrap;
}
.postNavigation {
    margin: 4rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    flex: 1 1 auto;
}
@media (max-width: 420px) {
    .postNavigation {
        gap: 2rem;
    }
}
@media (min-width: 421px) {
    .postNavigation {
        gap: 1rem;
    }
}
.prevpost,
.nextpost {
    max-width: 50%;
}
.nextpost {
    text-align: right;
}
@media (max-width: 420px) {
    .prevpost,
    .nextpost {
        max-width: 100%;
    }
}

#footer {
    padding: 6rem 0 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    flex: 1 1 auto;
    flex-wrap: wrap;
}
#copyright {
    font-family: 'Corben', sans-serif;
    font-weight: 300;
    font-size: 1rem;
}
#footer a {
    color: var(--main-color);
}
#footer a:hover {
    color: var(--acc-color-dark);
}
#footer ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 1rem;
}
#footer ul li {
    font-size: 1.8rem;
}

hr {
    height: 1px;
    max-width: 300px;
    margin: 3rem auto;
    padding: 0;
    border: none;
    background: linear-gradient(0.25turn, var(--main-color-cont), var(--acc-color-gray-dark), var(--main-color-cont));
}

blockquote {
    background-color: var(--acc-color-gray-light);
    font-size: 1.3rem;
    font-style: italic;
    padding: 0.8rem;
}
blockquote span {
    display: block;
    text-align: right;
    font-style: normal;
    font-size: 1.1rem;
}
.liquid {
    background-color: var(--main-color);
    color: var(--main-color-cont);
    border-radius: 0.4rem;
    padding: 0 0.8rem;
}
.samplecode {
    background-color: var(--acc-color-gray-light);
    display: inline-block;
    padding: 0.3rem 0.6rem;
}
.language-plaintext {
    font-family: 'Tajawal', sans-serif;
    font-size: 20px;
    font-weight: 300;
}
p img {
    max-width: 600px;
    border: 1px solid var(--main-color);
}
@media (max-width: 420px) {
    p img {
        max-width: 100%;
    }
}
@media (min-width:421px) and ( max-width:913px) {
    p img {
        max-width: 100%;
    }
}

.fa-hand-spock {
    color: var(--acc-color-spock);
}